.bannerFaq {
    background: #000;
    color: #fff;
    min-height: 180px;
    font-size: 32px;
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: center;
}

#orderDelivery .accordion-button {
    font-weight: 600;
    height:auto;
    font-size: 1.1rem;
    line-height: 30px;
}

#faqContainer .heading {
    font-size: x-large;
    margin: 1rem 0;
    font-weight: 900;
}

#orderDelivery .accordion-body {
    font-size: 1rem;
}

@media screen and (max-width:720px) {
    .bannerFaq {
        min-height: 100px;
        font-size: 26px;
    }
}